import React from 'react';
import Dimensions from 'react-dimensions';
import PropTypes from 'prop-types';
import ReactMapGL, {Marker, Popup} from 'react-map-gl';
import {Icon, Intent, Colors, Text, Tag} from '@blueprintjs/core';
import styled, {css} from 'styled-components';
import _isEmpty from 'lodash/isEmpty';

import 'mapbox-gl/dist/mapbox-gl.css';

const TOKEN = "pk.eyJ1IjoidGhvbWFzZG9sYSIsImEiOiJjam1kMjc1Z3YwZHVqM3ZvOTgwNnQyYnRkIn0.UNorgXPzOgTn0790NM1g3Q";
const MapStyles = `http://${window.location.hostname}:8080/styles/osm-bright/style.json`;
// const MapStyles = `http://${window.location.hostname}:8080/styles/klokantech-basic/style.json`;
// const MapStyles = "mapbox://styles/mapbox/streets-v9";

const CenterInactive = styled.div`
  background-color: ${Colors.RED5};
  border-radius: 50%;
  width: 40px;
  height: 40px;
  border: 10px solid ${Colors.RED4};
`;

const CenterActive = styled.div`
  background-color:  ${Colors.GREEN5};
  border-radius: 50%;
  width: 40px;
  height: 40px;
  border: 10px solid ${Colors.GREEN4};
`;

const SafeZone = styled.div`
  background-color: ${Colors.TURQUOISE4};
  border-radius: 50%;
  width: 40px;
  height: 40px;
  border: 10px solid ${Colors.TURQUOISE5};
`;

const QueryMark = styled.div`
  background-color: ${Colors.ORANGE4};
  border-radius: 50%;
  width: 40px;
  height: 40px;
  border: 10px solid ${Colors.ORANGE5};
`;

const Mark = styled.div`
  border-radius: 50%;
  width: 40px;
  height: 40px;

  ${
      props => props.status === 2 
        ? css`
            background-color: ${Colors.TURQUOISE4};
            border: 10px solid ${Colors.TURQUOISE5};
        ` 
        : (props.status === 1 
            ? css`
                background-color:  ${Colors.GREEN5};
                border: 10px solid ${Colors.GREEN4};
            ` 
            : css`
                background-color: ${Colors.RED5};
                border: 10px solid ${Colors.RED4};
            `
        )
  }
`;

const CenterMark = styled.div`
  border-radius: 50%;
  width: 40px;
  height: 40px;

  ${
      props => props.status === 1
        ? css`
            background-color:  ${Colors.GREEN5};
            border: 10px solid ${Colors.GREEN4};
        ` 
        : css`
            background-color: ${Colors.RED5};
            border: 10px solid ${Colors.RED4};
        `
  }
`;

const PopBox = styled.div`
  padding: 1rem;

  ${
    props => props.status === 2 
      ? css`
          color: ${Colors.TURQUOISE5};
      ` 
      : (props.status === 1 
          ? css`
              color: ${Colors.GREEN4};
          ` 
          : css`
              color: ${Colors.RED4};
          `
      )
    }
`;

const CenterPopBox = styled.div`
  padding: 1rem;

  ${
    props => props.status === 1
        ? css`
            border: 3px solid ${Colors.GREEN4};
        ` 
        : css`
            border: 3px solid ${Colors.RED4};
        `
    }
`;

const QueryPopBox = styled.div`
  padding: 1rem;
  border: 1px solid ${Colors.ORANGE4};
`;

class UberMap extends React.Component {

    state = {
        current: null,
        machine: null, 
        center: null,
        safeZone: {
            status: 2,
            latitude: 5.6260225,
            longitude: -0.1791814,
            name: "NIA"
        },
        viewport: {
            width: 400,
            height: 400,
            latitude: 5.6260225,
            longitude: -0.1791814,
            zoom: 13
        },
        settings: {
            dragPan: true,
            dragRotate: true,
            scrollZoom: true,
            touchZoom: true,
            touchRotate: true,
            keyboard: true,
            doubleClickZoom: true,
            minZoom: 9,
            maxZoom: 17,
            minPitch: 0,
            maxPitch: 85
          }
    };

    _showCurrentPopup = () => {
        const {current} = this.state;
        return current && (
            <Popup tipSize={5}
                anchor="bottom-right"
                offsetTop={-10}
                longitude={current.longitude}
                latitude={current.latitude}
                onClose={() => this.setState({current: null})}
                closeOnClick={true}>

                <PopBox status={current.status}>
                    <span>{current.name}</span>
                </PopBox>

            </Popup>
        );
    }

    _showQueryPopup = () => {
        const {query} = this.props;
        return query && [
            <Popup key="pop-up" tipSize={5}
                anchor="bottom"
                offsetTop={-10}
                longitude={query.longitude}
                latitude={query.latitude}>

                <QueryPopBox status={query.status}>
                    <span>{query.name}</span>
                </QueryPopBox>

            </Popup>,

            <Marker key="marker" latitude={query.latitude} longitude={query.longitude} offsetLeft={-20} offsetTop={-10}>
                <QueryMark 
                    status={query.status}/>
            </Marker>
        ];
    }

    _showSafeZonePopup = () => {
        const {safeZone} = this.state;
        return (
            <Popup tipSize={5}
                anchor="bottom"
                offsetTop={-10}
                closeButton={false}
                longitude={safeZone.longitude}
                latitude={safeZone.latitude}>

                <PopBox status={safeZone.status}>
                    <span>{safeZone.name}</span>
                </PopBox>

            </Popup>
        );
    }

    _showCenterPopup = () => {
        const {center} = this.state;
        return center && (
            <Popup tipSize={5}
                anchor="bottom-right"
                offsetTop={-10}
                longitude={center.longitude}
                latitude={center.latitude}
                onClose={() => this.setState({center: null})}
                closeOnClick={true}>

                <CenterPopBox status={center.status}>
                    <span style={{ fontWeight: 'bold' }}>{center.name}</span>

                    {center.deployments[0] && (
                        <div style={{ marginTop: '.5rem' }}>
                            {
                                center.deployments[0].machines.map(machine => {
                                    return (
                                        <div key={machine.name} style={{ display: 'flex', justifyContent: 'space-between' }} className="row">
                                            <Text ellipsize>{machine.name}</Text>
                                            <Tag minimal 
                                                intent={machine.power === "OFF" ? Intent.DANGER : Intent.SUCCESS}>{machine.power}</Tag>
                                        </div>
                                    );
                                })
                            }
                        </div>
                    )}
                </CenterPopBox>

            </Popup>
        );
    }

    render(){
        const {containerWidth, containerHeight, centers, center, current} = this.props;
        const {viewport, machine, safeZone, settings} = this.state;

        const currents = !_isEmpty(centers) ? [] : (machine ? [machine] : []);

        const viewportCenter = current 
            ? {longitude: current.longitude, latitude: center.latitude} 
            : (center 
                ? {longitude: center.longitude, latitude: center.latitude} 
                : {longitude: safeZone.longitude, latitude: safeZone.latitude});

        const viewportFull = {...viewport, width: containerWidth, height: containerHeight};

        return (
            <ReactMapGL
                {...viewportFull}
                {...settings}
                mapStyle={MapStyles}
                mapboxApiAccessToken={TOKEN}
                onViewportChange={(viewport) => this.setState({viewport})}
            >

                {
                    centers.map(center => {
                        return (
                            <Marker key={center.name} latitude={center.latitude} longitude={center.longitude} offsetLeft={-20} offsetTop={-10}>
                                <CenterMark 
                                    status={center.status} 
                                    onClick={() => {
                                        this.setState({center});
                                    }}/>
                            </Marker>
                        );
                    })
                }
                
                {
                    currents.map(current => {

                        return (
                            <Marker latitude={current.latitude} longitude={current.longitude} offsetLeft={-20} offsetTop={-10}>
                                <Mark 
                                    status={current.status} 
                                    onClick={() => {
                                        this.setState({current});
                                    }}/>
                            </Marker>
                        );
                    })
                }

                <Marker latitude={safeZone.latitude} longitude={safeZone.longitude} offsetLeft={-20} offsetTop={-10}>
                    <Mark 
                        status={safeZone.status}/>
                </Marker>

                
                {/* {this._showQueryPopup()} */}

                {this._showSafeZonePopup()}

                {this._showCurrentPopup()}

                {this._showCenterPopup()}

            </ReactMapGL>
        );
    }

    static propTypes = {
        centers: PropTypes.array.isRequired,
        machine: PropTypes.object,
    };
}


export default Dimensions()(UberMap);


